.bottom-container {
    display: grid;
    grid-template-columns: 0.3fr 1fr 1fr 1fr 1fr 1fr 0.3fr;
    width: 100%;
    height: 9vh;
    margin-top: 5px;
    justify-content: space-between;
  }
  
  .empty-box-bottom {
    height: 9vh;
    margin: 2px;
    background-size: cover;
    &:first-child {
      margin-left: 0px;
    }
    &:last-child {
      margin-right: 0px;
    }
  }
